var _ = Package.underscore._,
    package_name = "project",
    namespace = "project";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
TAPi18n.languages_names["kk"] = ["Kazakh","Қазақ тілі"];
if(_.isUndefined(TAPi18n.translations["kk"])) {
  TAPi18n.translations["kk"] = {};
}

if(_.isUndefined(TAPi18n.translations["kk"][namespace])) {
  TAPi18n.translations["kk"][namespace] = {};
}

_.extend(TAPi18n.translations["kk"][namespace], {});
TAPi18n._registerServerTranslator("kk", namespace);
