var _ = Package.underscore._,
    package_name = "project",
    namespace = "project";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
TAPi18n.languages_names["sl"] = ["Slovenian","slovenščina"];
if(_.isUndefined(TAPi18n.translations["sl"])) {
  TAPi18n.translations["sl"] = {};
}

if(_.isUndefined(TAPi18n.translations["sl"][namespace])) {
  TAPi18n.translations["sl"][namespace] = {};
}

_.extend(TAPi18n.translations["sl"][namespace], {"you_ve_just_been_invited":"Pravkar ste bili povabljeni.","start_posting":"Začnite objavljati!"});
TAPi18n._registerServerTranslator("sl", namespace);
